<template>
    <div class="container">
        <div class="header">
            <div class="header-bg"></div>
            <div class="roof">
                <span class="app-name">{{ appName }}</span>
                <input type="text" class="form-control form-control-sm" placeholder="输入任务编号搜索" />
                <i class="iconfont icon-saoma" />
            </div>
        </div>
        <div class="slideshow">
            <div class="carousel slide" data-bs-ride="carousel" data-bs-config='{"interval": 2000}'>
                <div class="carousel-indicators">
                    <button type="button" :data-bs-target="'#carousel-'+index" :data-bs-slide-to="index" :class="{'active':index==0}" :aria-current="index==0" :aria-label="index" v-for="(item,index) in carousels"></button>
                </div>
                <div class="carousel-inner">
                    <div class="carousel-item" :class="{'active':index==0}" v-for="(item,index) in carousels">
                        <img :src="item.url" :id="'carousel-'+index" class="d-block w-100 carousel-image" alt="加载失败">
                    </div>
                </div>
            </div>
        </div>
        <div class="hot-container">
            <div class="hot-box" v-for="(item,index) in hots">
                <div class="hot-image">
                    <img :src="item.cover" />
                </div>
                <span class="title">{{ item.title }}</span>
                <span class="type">{{ item.type }}</span>
                <span class="price">+{{ item.price }}</span>
            </div>
        </div>
        <div class="list">
            <div class="list-sort">
                <span class="sort-title">全部任务</span>
                <div class="sort-container">
                    <span class="sort-item g-active">默认排序</span>
                    <span class="sort-item">最新发布</span>
                    <span class="sort-item">佣金最高</span>
                </div>
            </div>
            <div class="list-container">
                <div class="list-item" v-for="(item,index) in list" :key="index" @click="taking(index)">
                    <div class="img">
                        <img :src="item.cover" />
                    </div>
                    <div class="info">
                        <div class="title">
                            <span>{{item.title}}</span>
                            <span>+{{ item.price }}</span>
                        </div>
                        <div class="type">
                            <span>ID:{{ item.id }}</span>
                            <span>{{item.type}}</span>
                        </div>
                        <div class="schedule">
                            <span>剩余 {{item.surplus}}/已结{{item.earned}} </span>
                            <span>接单</span>
                        </div>
                    </div>
                </div>
            </div>
            <div style="height: 80px;"></div>
        </div>
    </div>
</template>
<script>
import { mapGetters,mapActions } from 'vuex'
export default{
    data(){
        return{
        }
    },
    computed: {
        ...mapGetters(['appName','carousels','hots','list']),
    },
    methods:{
        ...mapActions(['ADD_TASK_ASYNC','JD_LIST_ASYNC']),
        taking(index){
            let _slef = this
            let lb = _slef.list[index]
            _slef.ADD_TASK_ASYNC({
                'id':lb.id,
                'title':lb.title,
                'type':lb.type,
                'price':lb.price,
                'dose':1,
                'cover':lb.cover
            }).then(res=>{
                _slef.$nextTick(() => {
                    _slef.JD_LIST_ASYNC(lb.id)
                })
                _slef.$forceUpdate();
            })
        }
    }
}
</script>
<style scoped>
.container{
    margin: 0;
    padding: 0;
    background-color: #f3f1f1;
}
.header{
    position: relative;
}
.header-bg{
    position: absolute;
    background-color: #e0c750;
    width: 100%;
    height: 160px;
    z-index: 0;
}
.roof{
    position: relative;
    display: flex;
    align-items: center;
    padding: 0 8px;
    z-index: 99;
}
.app-name{
    white-space: nowrap;
    margin-right: 15px;
    color: #fff;
}
.icon-saoma{
    margin-left: 15px;
    font-size: 24px;
    color: #fff;
}
.slideshow{
    margin: 8px 14px 0;
    border-radius: 8px;
    overflow: hidden;
}
.slideshow .carousel-inner .carousel-item .carousel-image{
    height: 150px;
}
.carousel-image{
    object-fit: cover;
}
.hot-container{
    display: flex;
    padding: 5px 15px;
    justify-content: space-between;
}
.hot-box{
    display: flex;
    flex-direction: column;
    width: 32%;
    background-color: #fff;
    border-radius: 6px;
    overflow: hidden;
    text-align: center;
    padding-bottom: 6px;
    font-size: 16px;
}
.hot-image{
    position: relative;
    max-width: 100%;
    height: 65px;
    border-radius: 3px;
}
.hot-image>img{
    display: block;
    width: 100%; /* Ensure the image is responsive */
    height: 100%;
    object-fit: cover;
}
.hot-box>.title{
    margin-top: 5px;
}
.hot-box>.type{
    font-size: 12px;
    color: #999;
    -webkit-transform: scale(.8);
    transform: scale(.8);
    line-height: 2;
}
.hot-box>.price{
    color: #ec602d;
}
.list{
    margin-top: 5px;
}
.list-sort{
    display: flex;
    align-items: center;
    background-color: #fff;
    padding: 10px 15px;
}
.sort-title{
    font-size: 18px;
    font-weight: 700;
    color: #333;
}
.sort-container{
    margin: 1px 0 0 12px;
}
.sort-item{
    font-size: 14px;
    position: relative;
    margin-right: 9px;
    color: #999;
}
.g-active{
    color: #333;
}
.g-active::after{
    content: "";
    position: absolute;
    left: 50%;
    margin-left: -.88rem;
    bottom: -3px;
    width: 2em;
    height: 3px;
    background-color: #ec602d;
}
.list-container{
    display: flex;
    flex-direction: column;
    padding: 8px 15px;
}
.list-item{
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    align-items: center;
    background-color: #fff;
    margin-bottom: 6px;
    padding: 6px;
}
.list-item>.img{
    height: 80px;
    width: 120px;
    object-fit: contain;
    border-radius: 6px;
    overflow: hidden;
}
.list-item>.img>img{
    width: 100%;
    height: 100%;
}
.list-item>.info{
    padding: 5px;
    flex: 1;
}
.list-item>.info>.title{
    display: flex;
    justify-content: space-between;
    font-size: 17px;
}
.list-item>.info>.title>span:last-child{
    color: #ec602d;
}
.list-item>.info>.type{
    display: flex;
    margin-top: 2px;
}
.list-item>.info>.type>span{
    display: block;
    background-color: #f5f5f5;
    margin-right: -8px;
    font-size: 12px;
    padding: 4px 6px;
    border-radius: 4px;
    -webkit-transform: scale(.8);
    transform: scale(.8);
    -webkit-transform-origin: 0;
    transform-origin: 0;
}

.list-item>.info>.schedule{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #999;
    font-size: 14px;
    margin-top: 1px;
}
.list-item>.info>.schedule>span:last-child{
    background-color: #ec602d;
    color: #fff;
    padding: 2px 6px;
    border-radius: 4px;
}
</style>